// Created By : Sujay

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { uploadFile } from "react-s3";
import EDU_AXIOS from "src/helpers/edu_axios";
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCol,
  CFormInput,
  CRow,
  CFormLabel,
  CFormSelect,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
  CFormTextarea,
} from "@coreui/react-pro";
import { makeStyles } from "@material-ui/core/styles";
import {
  faSave,
  faTimesCircle,
  faCheckCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Input from "@material-ui/core/Input";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import ListItemText from "@material-ui/core/ListItemText";
import Select from "@material-ui/core/Select";
import Checkbox from "@material-ui/core/Checkbox";

const config = {
  bucketName: process.env.REACT_APP_BUCKET_NAME,
  dirName: process.env.REACT_APP_SCHOOL_LOGO_DIR_NAME,
  region: process.env.REACT_APP_REGION,
  accessKeyId: process.env.REACT_APP_ACCESS_ID,
  secretAccessKey: process.env.REACT_APP_ACCESS_KEY,
};

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(2),
    },
  },
  input: {
    display: "none",
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
  },
  chips: {
    display: "flex",
    flexWrap: "wrap",
  },
  chip: {
    margin: 2,
  },
  noLabel: {
    marginTop: theme.spacing(3),
  },
}));

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const classs = ["SMS", "LMS", "Video Conferencing", "Demo"];

const SchoolCreation = () => {
  // set form values
  const classes = useStyles();
  const [userID, setUSerID] = useState("");
  const [school_name, setSchool_name] = useState("");
  const [address, setAddress] = useState("");
  const [pin, setPin] = useState("");
  const [website, setWebsite] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");
  const [selectedFile, setSelectedFile] = useState("");
  const [academics_from, setAcademics_from] = useState("");
  const [academics_to, setacademics_to] = useState("");
  const [license_start, setLicense_start] = useState("");
  const [license_end, setLicense_end] = useState("");
  const [total_users, setTotal_users] = useState("");
  const [password, setPassword] = useState("");
  const [password2, setPassword2] = useState("");
  const [state_name, setState_name] = useState([]);
  const [country_code, setCountry_code] = useState();
  const [city, setCity] = useState([]);
  const [stateName, setStateName] = useState();
  const [cityName, setCityName] = useState();
  const [visible, setVisible] = useState(false);
  const [classNames, setClassNames] = React.useState([]);
  const [countrylist, setCountryList] = useState([]);
  const [chainSchools, setChainSchools] = useState([]);
  const [chainSchoolId, setCahinSchoolId] = useState("");
  const [logoName, setLogoName] = useState("");

  // Defining Errors
  const [userIDErr, setUserIDErr] = useState({});
  const [schoolNameErr, setSchoolNameErr] = useState({});
  const [pinErr, setPinErr] = useState({});
  const [stateErr, setStateErr] = useState({});
  const [cityErr, setCityErr] = useState({});
  const [countryErr, setCountryErr] = useState({});
  const [addressErr, setAddressErr] = useState({});
  const [webErr, setWebErr] = useState({});
  const [emailErr, setEmailErr] = useState({});
  const [mobileErr, setMobileErr] = useState({});
  const [passwordErr, setPasswordErr] = useState({});
  const [password2Err, setPassword2Err] = useState({});
  const [calStartErr, setCalStartErr] = useState({});
  const [calEndErr, setCalEndErr] = useState({});
  const [academicStartErr, setacAdemicStartErr] = useState({});
  const [academicEndErr, setAcademicEndErr] = useState({});
  const [totalErr, setTotalErr] = useState({});

  // to get country list from server
  useEffect(() => {
    EDU_AXIOS.get(process.env.REACT_APP_API_PATH + "/auth/get-countries")
      .then((response) => {
        let countryArray = response.data.data;
        setCountryList(countryArray);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  // to get chain school list from server
  useEffect(() => {
    EDU_AXIOS.get(
      process.env.REACT_APP_API_PATH + "/auth/get-chain-school-list"
    )
      .then((response) => {
        setChainSchools(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  // set country code before calling state list api
  const selectState = (e) => {
    var countryCode = e.target.value;
    setCountry_code(countryCode);
  };

  // get states by country code
  useEffect(() => {
    // check country code is set or not
    if (country_code) {
      EDU_AXIOS.get(
        process.env.REACT_APP_API_PATH + `/auth/get-states/${country_code}`
      )
        .then((resp) => {
          let stateArray = resp.data.data;
          setState_name(stateArray);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }, [country_code]);

  // get cities of a state
  useEffect(() => {
    // check coundry code and state name is set or not
    if (stateName) {
      EDU_AXIOS.get(
        process.env.REACT_APP_API_PATH +
          `/auth/get-cities/${country_code}/${stateName}`
      )
        //EDU_AXIOS.get(cityURL)
        .then((respo) => {
          let cityArray = respo.data.data;

          setCity(cityArray);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }, [stateName]);

  const handleChange = (event) => {
    setClassNames(event.target.value);
  };

  const [inputList, setInputList] = useState([
    { class_name: "", total_student: "" },
  ]);

  // handle input change
  const handleInputChange = (e, index) => {
    const { name, value } = e.target;
    const list = [...inputList];
    list[index][name] = value;
    setInputList(list);
    // console.log(inputList,"B");
  };

  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };

  // handle click event of the Add button
  const handleAddClick = () => {
    setInputList([...inputList, { class_name: "", total_student: "" }]);
  };

  // get a file to upload
  const handleFileInput = (e) => {
    setLogoName(e.target.files[0].name);
    setSelectedFile(e.target.files[0]);
  };
  // upload selected file to s3 bucket
  const handleUpload = async (file) => {
    uploadFile(file, config)
      .then((data) => console.log(data, "Logo uploaded successfully"))
      .catch((err) => console.error(err, ""));
  };

  // const [validated, setValidated] = useState(false);
  const onSubmit = async (e) => {
    e.preventDefault();
    const isValid = await formValidation();
    // check form is valid or not
    if (isValid) {
      return await EDU_AXIOS.post(
        process.env.REACT_APP_API_PATH + "/auth/create-normal-school",
        {
          name: school_name,
          username: userID,
          password: password,
          email: email,
          mobile: mobile,
          address: address,
          pin_code: pin,
          state: stateName,
          city: cityName,
          website: website,
          acadmics_from: academics_from,
          acadmics_to: academics_to,
          license_start: license_start,
          license_end: license_end,
          classes: inputList,
          total_users: total_users,
          license_type: classNames,
          logo: logoName,
          chain_school_id: chainSchoolId,
        }
      )
        .then(async (res) => {
          e.target.reset();
          alert(res.data.message);
        })
        .catch((err) => {
          alert(err);
        });
    }
  };

  const formValidation = async () => {
    const userIDErr = {};
    const schoolNameErr = {};
    const pinErr = {};
    const webErr = {};
    const emailErr = {};
    const mobileErr = {};
    const passwordErr = {};
    const password2Err = {};
    const stateErr = {};
    const cityErr = {};
    const countryErr = {};
    const addressErr = {};
    const calStartErr = {};
    const calEndErr = {};
    const academicStartErr = {};
    const academicEndErr = {};
    const totalErr = {};

    let isValid = true;
    // Validations for different fields

    if (!userID.trim()) {
      userIDErr.UserIDrequired = "Required";
    } else if (/[^a-zA-Z0-9_.]/.test(userID) || userID.trim().length < 6) {
      userIDErr.UserIDinvalid =
        "UserID is Invalid (@ , # not supported) / < 6 Digits";
      isValid = false;
    }

    if (!license_start.trim()) {
      calStartErr.calShort = " Required";
      isValid = false;
    }
    if (!license_end.trim()) {
      calEndErr.cal = " Required";
      isValid = false;
    }
    if (!academics_from.trim()) {
      academicStartErr.start = " Required";
      isValid = false;
    }
    if (!academics_to.trim()) {
      academicEndErr.end = " Required";
      isValid = false;
    }
    if (!total_users.trim()) {
      totalErr.total = " Required";
      isValid = false;
    }

    if (!school_name.trim()) {
      schoolNameErr.schoolNameShort = " Required";
      isValid = false;
    }

    if (!pin.trim()) {
      pinErr.pinreq = " Required";
      isValid = false;
    } else if (!/[123456789][0-9]{5}/.test(pin) || pin.trim().length > 6) {
      pinErr.pinshort = "PIN should be 6 Digits only / PIN Can't Start with 0";
      isValid = false;
    }
    if (!address.trim()) {
      addressErr.addressShort = " Required";
      isValid = false;
    }

    if (!email.trim()) {
      emailErr.emailrequired = "Required";
      isValid = false;
    } else if (!/\S+@\S+\.\S+/.test(email)) {
      emailErr.emailinvalid = "Email address is invalid";
      isValid = false;
    }

    if (!mobile.trim()) {
      mobileErr.mobreq = " Required";
      isValid = false;
    } else if (mobile.trim().length > 10 || !/[6789][0-9]{9}/.test(mobile)) {
      mobileErr.mobshort = "Mobile number must be Valid & of 10 Digits Only";
      isValid = false;
    }

    if (!password.trim()) {
      passwordErr.passreq = " Required";
      isValid = false;
    } else if (password.trim().length < 6 || password.trim().length > 20) {
      passwordErr.passshort = "Invalid / < 6 Digits";
      isValid = false;
    }

    if (!password2.trim()) {
      passwordErr.pass2req = " ";
      isValid = false;
    } else if (password2 !== password) {
      password2Err.pass2short = "Doesn't Match ";
      isValid = false;
    }
    // setting errors in validation
    setUserIDErr(userIDErr);
    setSchoolNameErr(schoolNameErr);
    setPinErr(pinErr);
    setWebErr(webErr);
    setEmailErr(emailErr);
    setMobileErr(mobileErr);
    setPasswordErr(passwordErr);
    setPassword2Err(password2Err);
    setCityErr(cityErr);
    setStateErr(stateErr);
    setCountryErr(countryErr);
    setAddressErr(addressErr);
    setCalStartErr(calStartErr);
    setCalEndErr(calEndErr);
    setacAdemicStartErr(academicStartErr);
    setAcademicEndErr(academicEndErr);
    setTotalErr(totalErr);
    return isValid;
  };
  return (
    //  School Creation Form
    <form onSubmit={onSubmit} id="ev_create_normal_school">
      <CRow>
        <CCol>
          <CCard>
            <CCardBody>
              <CRow>
                <CCol md="8" lg="6" xl="8">
                  <h5>Create Normal School</h5>
                </CCol>
              </CRow>
              <p className="text-muted">Register a Normal School</p>

              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel htmlFor="validationCustom01">
                    School Name :
                  </CFormLabel>

                  <CFormInput
                    type="text"
                    id="req1"
                    key="sname"
                    placeholder="Name of School"
                    value={school_name}
                    onChange={(e) => {
                      setSchool_name(e.target.value);
                    }}
                  />
                  {Object.keys(schoolNameErr).map((key) => {
                    return (
                      <div style={{ color: "red" }}>{schoolNameErr[key]}</div>
                    );
                  })}
                </CCol>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel>User Name :</CFormLabel>
                  <CFormInput
                    key="user"
                    id="school_name"
                    placeholder="Unique User Name"
                    value={userID}
                    onChange={(e) => {
                      setUSerID(e.target.value);
                    }}
                  />
                  {Object.keys(userIDErr).map((key) => {
                    return <div style={{ color: "red" }}>{userIDErr[key]}</div>;
                  })}
                </CCol>
              </CRow>
              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> Password :</CFormLabel>
                  <CFormInput
                    key="pass"
                    id=""
                    type="password"
                    value={password}
                    onChange={(e) => {
                      setPassword(e.target.value);
                    }}
                  />
                  {Object.keys(passwordErr).map((key) => {
                    return (
                      <div style={{ color: "red" }}>{passwordErr[key]}</div>
                    );
                  })}
                </CCol>
                <CCol>
                  <CFormLabel> Re-Enter Password :</CFormLabel>
                  <CFormInput
                    id=""
                    key="re-pass"
                    type="password"
                    value={password2}
                    onChange={(e) => {
                      setPassword2(e.target.value);
                    }}
                  />
                  {Object.keys(password2Err).map((key) => {
                    return (
                      <div style={{ color: "red" }}>{password2Err[key]}</div>
                    );
                  })}
                </CCol>
              </CRow>

              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel>Email ID :</CFormLabel>
                  <CFormInput
                    id=""
                    key="email"
                    placeholder="School Email ID"
                    type="email"
                    value={email}
                    onChange={(e) => {
                      setEmail(e.target.value);
                    }}
                  />
                  {Object.keys(emailErr).map((key) => {
                    return <div style={{ color: "red" }}>{emailErr[key]}</div>;
                  })}
                </CCol>

                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> Mobile :</CFormLabel>
                  <CFormInput
                    id=""
                    key="mobile"
                    placeholder="Mobile Number"
                    type="number"
                    value={mobile}
                    onChange={(e) => {
                      setMobile(e.target.value);
                    }}
                  />
                  {Object.keys(mobileErr).map((key) => {
                    return <div style={{ color: "red" }}>{mobileErr[key]}</div>;
                  })}
                </CCol>
              </CRow>

              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> School Address:</CFormLabel>
                  <CFormTextarea
                    id=""
                    key="address"
                    placeholder="Address of Your School"
                    rows="1"
                    value={address}
                    onChange={(e) => {
                      setAddress(e.target.value);
                    }}
                  />
                  {Object.keys(addressErr).map((key) => {
                    return (
                      <div style={{ color: "red" }}>{addressErr[key]}</div>
                    );
                  })}
                </CCol>

                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> Pin:</CFormLabel>
                  <CFormInput
                    id=""
                    key="pin"
                    placeholder="Pin Code"
                    type="number"
                    value={pin}
                    onChange={(e) => {
                      setPin(e.target.value);
                    }}
                  />
                  {Object.keys(pinErr).map((key) => {
                    return <div style={{ color: "red" }}>{pinErr[key]}</div>;
                  })}
                </CCol>
              </CRow>

              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> Country:</CFormLabel>
                  <CFormSelect
                    key="country"
                    value={countrylist.name}
                    onChange={selectState}
                  >
                    <option>Select the Country</option>
                    {countrylist.map((countrylist) => {
                      return (
                        <option value={countrylist.country_code}>
                          {countrylist.name}
                        </option>
                      );
                    })}
                  </CFormSelect>
                  {Object.keys(countryErr).map((key) => {
                    return (
                      <div style={{ color: "red" }}>{countryErr[key]}</div>
                    );
                  })}
                </CCol>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> State:</CFormLabel>
                  <CFormSelect
                    key="state"
                    value={state_name.name}
                    onChange={(e) => {
                      setStateName(e.target.value);
                    }}
                  >
                    <option disabled>Select the Country First</option>
                    {state_name.map((state_name) => {
                      return (
                        <option value={state_name.state_code}>
                          {state_name.name}
                        </option>
                      );
                    })}
                  </CFormSelect>
                  {Object.keys(stateErr).map((key) => {
                    return <div style={{ color: "red" }}>{stateErr[key]}</div>;
                  })}
                </CCol>
              </CRow>

              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> City:</CFormLabel>
                  <CFormSelect
                    key="city"
                    value={city.name}
                    onChange={(e) => {
                      setCityName(e.target.value);
                    }}
                  >
                    <option disabled>Select the State First</option>
                    {city.map((city) => {
                      return <option value={city.name}>{city.name}</option>;
                    })}
                  </CFormSelect>
                  {Object.keys(cityErr).map((key) => {
                    return <div style={{ color: "red" }}>{cityErr[key]}</div>;
                  })}
                </CCol>

                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> Region:</CFormLabel>
                  <CFormSelect key="reg" custom name="" id="">
                    <option>Open this select menu</option>
                    <option value="East">East </option>
                    <option value="West">West</option>
                    <option value="North">North</option>
                    <option value="South">South</option>
                    <option value="International">International</option>
                  </CFormSelect>
                </CCol>
              </CRow>
              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> Board:</CFormLabel>
                  <CFormSelect key="board" custom name="" id="">
                    <option>Open this select menu</option>
                    <option value="Cbse">CBSE </option>
                    <option value="Icse">ICSE</option>
                    <option value="IB">IB</option>
                    <option value="Igcse">IGCSE</option>
                    <option value="State">STATE BOARD</option>
                  </CFormSelect>
                </CCol>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel>Website Of School :</CFormLabel>
                  <CFormInput
                    id=""
                    key="website"
                    placeholder="Example ==> www.websiteofschool.com"
                    value={website}
                    onChange={(e) => {
                      setWebsite(e.target.value);
                    }}
                  />
                  {Object.keys(webErr).map((key) => {
                    return <div style={{ color: "red" }}>{webErr[key]}</div>;
                  })}
                </CCol>
              </CRow>

              <CRow>
                <CCol>
                  <CFormLabel> Chain School List:</CFormLabel>
                  <CFormSelect
                    key="chain_schools"
                    value={chainSchools.school_id}
                    onChange={(e) => {
                      setCahinSchoolId(e.target.value);
                    }}
                  >
                    <option value="">Select the chain school</option>
                    {chainSchools.map((ch) => {
                      return <option value={ch.school_id}>{ch.name}</option>;
                    })}
                  </CFormSelect>
                </CCol>
              </CRow>
              {inputList.map((x, i) => {
                return (
                  <div className="box">
                    <CRow>
                      {/* <CCol md="9" lg="7" xl="6">
                        <CFormLabel> Select Class:</CFormLabel>
                        <CFormSelect
                          custom
                          name="class_name"
                          id=""
                          key="clas"
                          value={x.class_name}
                          onChange={(e) => handleInputChange(e, i)}
                        >
                          <option>Open this select menu</option>
                          <option value="Nursery">Nursery </option>
                          <option value="LKG">LKG </option>
                          <option value="UKG">UKG</option>
                          <option value="1">1</option>
                          <option value="2">2</option>
                          <option value="3">3</option>
                          <option value="4">4</option>
                          <option value="5">5</option>
                          <option value="6">6</option>
                          <option value="7">7</option>
                          <option value="8">8</option>
                          <option value="9">9</option>
                          <option value="10">10</option>
                          <option value="11">11</option>
                          <option value="12">12</option>
                        </CFormSelect>
                      </CCol> */}

                      <CCol md="5" lg="5" xl="5">
                        <CFormLabel> Total License Limit :</CFormLabel>
                        <CFormInput
                          key="stud"
                          id="total_student"
                          name="total_student"
                          placeholder=""
                          value={x.total_student}
                          onChange={(e) => handleInputChange(e, i)}
                          type="number"
                        />
                      </CCol>

                      {/* <CCol>
                        <CFormLabel> </CFormLabel>
                        <div className="btn-box">
                          {inputList.length !== 1 && (
                            <button
                              className="butt"
                              onClick={() => handleRemoveClick(i)}
                            >
                              Remove
                            </button>
                          )}
                          <span> </span>
                          {inputList.length - 1 === i && (
                            <button onClick={handleAddClick}>Add</button>
                          )}
                        </div>
                      </CCol> */}
                    </CRow>
                  </div>
                );
              })}

              <CRow>
                <CCol md="12" lg="12" xl="12">
                  <CFormLabel> Total Users</CFormLabel>
                  <CFormInput
                    key="tot"
                    id=""
                    placeholder=""
                    value={total_users}
                    onChange={(e) => {
                      setTotal_users(e.target.value);
                    }}
                  />
                  {Object.keys(totalErr).map((key) => {
                    return <div style={{ color: "red" }}>{totalErr[key]}</div>;
                  })}
                </CCol>
              </CRow>
              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> Academics (From):</CFormLabel>
                  <CFormSelect
                    key="acaf"
                    custom
                    name=""
                    id=""
                    value={academics_from}
                    onChange={(e) => {
                      setAcademics_from(e.target.value);
                    }}
                  >
                    <option>Open this select menu</option>
                    <option value="January">January </option>
                    <option value="February">February</option>
                    <option value="March">March</option>
                    <option value="April">April</option>
                    <option value="May">May</option>
                    <option value="June">June</option>
                    <option value="July">July</option>
                    <option value="August">August</option>
                    <option value="September">September</option>
                    <option value="October">October</option>
                    <option value="November">November</option>
                    <option value="December">December</option>
                  </CFormSelect>
                  {Object.keys(academicStartErr).map((key) => {
                    return (
                      <div style={{ color: "red" }}>
                        {academicStartErr[key]}
                      </div>
                    );
                  })}
                </CCol>

                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> Academics (To):</CFormLabel>
                  <CFormSelect
                    key="acat"
                    custom
                    name=""
                    id=""
                    value={academics_to}
                    onChange={(e) => {
                      setacademics_to(e.target.value);
                    }}
                  >
                    <option>Open this select menu</option>
                    <option value="January">January </option>
                    <option value="February">February</option>
                    <option value="March">March</option>
                    <option value="April">April</option>
                    <option value="May">May</option>
                    <option value="June">June</option>
                    <option value="July">July</option>
                    <option value="August">August</option>
                    <option value="September">September</option>
                    <option value="October">October</option>
                    <option value="November">November</option>
                    <option value="December">December</option>
                  </CFormSelect>
                  {Object.keys(academicEndErr).map((key) => {
                    return (
                      <div style={{ color: "red" }}>{academicEndErr[key]}</div>
                    );
                  })}
                </CCol>
              </CRow>

              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> License (Start):</CFormLabel>
                  <CFormInput
                    id=""
                    key="lics"
                    placeholder=""
                    value={license_start}
                    onChange={(e) => {
                      setLicense_start(e.target.value);
                    }}
                    type="date"
                  />
                  {Object.keys(calStartErr).map((key) => {
                    return (
                      <div style={{ color: "red" }}>{calStartErr[key]}</div>
                    );
                  })}
                </CCol>

                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> License (End):</CFormLabel>
                  <CFormInput
                    id=""
                    key="lice"
                    placeholder=""
                    value={license_end}
                    onChange={(e) => {
                      setLicense_end(e.target.value);
                    }}
                    type="date"
                  />
                  {Object.keys(calEndErr).map((key) => {
                    return <div style={{ color: "red" }}>{calEndErr[key]}</div>;
                  })}
                </CCol>
              </CRow>

              <CRow>
                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> License Type:</CFormLabel>
                  <FormControl className={classes.formControl}>
                    <Select
                      key="ltype"
                      labelId="demo-mutiple-checkbox-labels"
                      id="demo-mutiple-checkboxs"
                      variant="outlined"
                      multiple
                      value={classNames}
                      onChange={handleChange}
                      input={<Input />}
                      renderValue={(selected) => selected.join(", ")}
                      MenuProps={MenuProps}
                    >
                      {classs.map((name) => (
                        <MenuItem key={name} value={name}>
                          <Checkbox checked={classNames.indexOf(name) > -1} />
                          <ListItemText primary={name} />
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </CCol>

                <CCol md="9" lg="7" xl="6">
                  <CFormLabel> Upload Logo:</CFormLabel>
                  <CFormInput
                    key="logo"
                    type="file"
                    id=""
                    onChange={handleFileInput}
                  />
                </CCol>
              </CRow>
            </CCardBody>

            <CCardFooter>
              <CRow>
                <CCol md={3}>
                  <CButton
                    block
                    //variant="outline"
                    color="light"
                    onClick={() => setVisible(!visible)}
                    style={{ float: "left" }}
                  >
                    <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                  </CButton>

                  <CModal visible={visible} onDismiss={() => setVisible(false)}>
                    <CModalHeader onDismiss={() => setVisible(false)}>
                      <CModalTitle>Cancel</CModalTitle>
                    </CModalHeader>

                    <CModalBody>Do You Want To Cancel ?</CModalBody>
                    <CModalFooter>
                      <CButton color="light" onClick={() => setVisible(false)}>
                        <FontAwesomeIcon icon={faTimesCircle} /> No
                      </CButton>
                      <Link to="/">
                        <CButton color="dark">
                          <FontAwesomeIcon icon={faCheckCircle} /> Yes
                        </CButton>
                      </Link>
                    </CModalFooter>
                  </CModal>

                  <CButton
                    color="dark"
                    type="submit"
                    style={{ marginLeft: "15px" }}
                    disabled={
                      !school_name ||
                      !userID ||
                      !email ||
                      !password ||
                      !mobile ||
                      !address ||
                      !pin ||
                      !total_users ||
                      !academics_from ||
                      !academics_to ||
                      !license_start ||
                      !license_end
                    }
                    onClick={() => handleUpload(selectedFile)}
                  >
                    <FontAwesomeIcon icon={faSave} /> Save
                  </CButton>
                </CCol>
              </CRow>
            </CCardFooter>
          </CCard>
        </CCol>
      </CRow>
    </form>
  );
};

export default SchoolCreation;
